import React, { Component } from 'react';
import { Form, Input, Button, } from 'antd';

const FormItem = Form.Item;

class LoginForm extends Component {
    constructor(props) {
        super(props);


    }

    render() {
        const {form: {getFieldDecorator}, handleSubmit } = this.props;
        return(
        <Form onSubmit={ handleSubmit } className='login-form'>
            <FormItem>
                {getFieldDecorator('userName', { rules: [{required: true, message: ''}],})(<Input  placeholder="" />)}
            </FormItem>
            <FormItem>
                {getFieldDecorator('password', {rules: [{required: true,message: ''}],})(<Input type="password" placeholder="" />)}
            </FormItem>
            <FormItem>
              <Button type="primary" htmlType="submit" className="btn"></Button>
            </FormItem>
      </Form>
    )}
}

export default Form.create()(LoginForm)


