import React, { Component } from 'react';
import { Form, Input, Button, } from 'antd';

const FormItem = Form.Item;

class RegisteForm extends Component {
    constructor(props) {
        super(props);


    }

    checkPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
          callback('');
        } else {
          callback();
        }
    }



    render() {
        const {form: {getFieldDecorator}, handleRegisterSubmit } = this.props;
        return(
        <Form onSubmit={ handleRegisterSubmit } className='login-form register-form'>
            <FormItem hasFeedback>
                {getFieldDecorator('userName', { rules: [{required: true, message: ''}],})(<Input  placeholder="" />)}
            </FormItem>
            <FormItem hasFeedback>
                {getFieldDecorator('email', { rules: [{type: 'email', message: 'The input is not valid E-mail!', }, { required: true, message: '',}], })(<Input placeholder="E-mail" />) }
            </FormItem>
            <FormItem hasFeedback>
                {getFieldDecorator('password', {rules: [{required: true,message: ''}],})(<Input type="password" placeholder="" />)}
            </FormItem>
             <FormItem hasFeedback>
              {getFieldDecorator('confirmPassword', {
                rules: [{
                  required: true, message: '',
                }, {
                  validator: this.checkPassword,
                }],
                })(
                <Input type="password" onBlur={this.handleConfirmBlur} placeholder='' />
                )}
            </FormItem>
            <FormItem>
              <Button type="primary" htmlType="submit" className="btn"></Button>
            </FormItem>
      </Form>
    )}
}

export default Form.create()(RegisteForm)


