import React, {Component} from 'react';
import jQuery from 'jquery';
// import editormd from './editormd';
// import EditorMD from 'editor.md/editormd';
import './editormd.css';
window.jQuery = window.$ = jQuery;

let loadEditorMD = () => {
    return new Promise((resolve, reject) => {
        if (window.editormd) {
            resolve(true);
        } else {
            let script = document.createElement("script");
            script.async = true;
            script.onload = () => {
                resolve(true);
            };
            script.onerror = () => {
                reject(false);
            };
            script.src = 'http://oyfapuy1b.bkt.clouddn.com/editormd/editormd.min.js';
            document.head.appendChild(script);
        }
    });
};
let defaultConfig = {
    // ID
    id: 'EditorID' + new Date().getTime(),
    width: "100%",
    height: 740,
    // 
    path: 'http://oyfapuy1b.bkt.clouddn.com/editormd/lib/',
    // theme : "dark",
    // previewTheme : "dark",
    // editorTheme : "pastel-on-dark",
    markdown: `## test`,
    codeFold: true,
    // syncScrolling : false,
    saveHTMLToTextarea: true,    //  HTML  Textarea
    searchReplace: true,
    // watch : false,                // 
    htmlDecode: "style,script,iframe|on*",            //  HTML 
    // toolbar  : false,             //
    // previewCodeHighlight : false, //  HTML 
    emoji: true,
    taskList: true,
    tocm: true,         // Using [TOCM]
    tex: true,                   // TeX
    flowChart: true,             // 
    sequenceDiagram: true,       // /,
    // dialogLockScreen : false,   // true
    // dialogShowMask : false,     // true
    // dialogDraggable : false,    // true
    // dialogMaskOpacity : 0.4,    // 0.1
    // dialogMaskBgColor : "#000", // #fff
    imageUpload: true,
    imageFormats: ["jpg", "jpeg", "gif", "png", "bmp", "webp"],
    imageUploadURL: "./php/upload.php",
    onload: function(){}
};

class Editor extends Component {
    static defaultProps = {
        config: {}
    };

    async componentDidMount(){
        await loadEditorMD();
        let {config} = this.props;
        config = Object.assign({}, defaultConfig, config);

        let {
            id, width, height, path, theme, previewTheme, editorTheme, markdown, codeFold, syncScrolling,
            saveHTMLToTextarea, searchReplace, watch, htmlDecode, toolbar, previewCodeHighlight, emoji,
            taskList, tocm, tex, flowChart, sequenceDiagram, dialogLockScreen, dialogShowMask, dialogDraggable,
            dialogMaskOpacity, dialogMaskBgColor, imageUpload, imageFormats, imageUploadURL, onload
        } = config;

        // 
        if(path !== defaultConfig.path){
            console.warn('Editor warning: Static resource address has changed, if you know exactly what you\'re doing, ignore this warning');
        }

        let editor = editormd(id, {
            width: width,
            height: height,
            path: path,
            theme: theme,
            previewTheme: previewTheme,
            editorTheme: editorTheme,
            markdown: markdown,
            codeFold: codeFold,
            syncScrolling: syncScrolling,
            saveHTMLToTextarea: saveHTMLToTextarea,    //  HTML  Textarea
            searchReplace: searchReplace,
            watch: watch,                // 
            htmlDecode: htmlDecode,            //  HTML 
            toolbar: toolbar,             //
            previewCodeHighlight: previewCodeHighlight, //  HTML 
            emoji: emoji,
            taskList: taskList,
            tocm: tocm,         // Using [TOCM]
            tex: tex,                   // TeX
            flowChart: flowChart,             // 
            sequenceDiagram: sequenceDiagram,       // /,
            dialogLockScreen: dialogLockScreen,   // true
            dialogShowMask: dialogShowMask,     // true
            dialogDraggable: dialogDraggable,    // true
            dialogMaskOpacity: dialogMaskOpacity,    // 0.1
            dialogMaskBgColor: dialogMaskBgColor, // #fff
            imageUpload: imageUpload,
            imageFormats: imageFormats,
            imageUploadURL: imageUploadURL,
            onload: function(){
                onload(editor, this);
            }
        });

    }

    render(){
        let {config} = this.props;
        config = Object.assign({}, defaultConfig, config);

        return (<div id={config.id}></div>);
    }
}

let defaultShowConfig = {
    id: 'EditorShowID' + new Date().getTime(),
    path: 'http://oyfapuy1b.bkt.clouddn.com/editormd/lib/',
    gfm: true,
    toc: true,
    tocm: true,
    tocStartLevel: 1,
    tocTitle: '',
    tocDropdown: true,
    tocContainer: '',
    markdown: 'what can i do for you',
    markdownSourceCode: true,
    htmlDecode: true,
    autoLoadKaTeX: true,
    pageBreak: true,
    atLink: true,
    emailLink: true,
    tex: true,
    taskList: true,
    emoji: true,
    flowChart: true,
    sequenceDiagram: true,
    previewCodeHighlight: true
};

class EditorShow extends Component {
    static defaultProps = {
        config: {}
    };

    async componentDidMount(){
        await loadEditorMD();
        let {config} = this.props;
        config = Object.assign({}, defaultShowConfig, config);

        let {
            id, gfm, toc, tocm, tocStartLevel, tocTitle, tocDropdown, tocContainer, markdown, markdownSourceCode,
            htmlDecode, autoLoadKaTeX, pageBreak, atLink, emailLink, tex, taskList, emoji, flowChart,
            sequenceDiagram, previewCodeHighlight, path
        } = config;

        this._init(path, function(){
            editormd.markdownToHTML(id, {
                gfm: gfm,
                toc: toc,
                tocm: tocm,
                tocStartLevel: tocStartLevel,
                tocTitle: tocTitle,
                tocDropdown: tocDropdown,
                tocContainer: tocContainer,
                markdown: markdown,
                markdownSourceCode: markdownSourceCode,
                htmlDecode: htmlDecode,
                autoLoadKaTeX: autoLoadKaTeX,
                pageBreak: pageBreak,
                atLink: atLink,    // for @link
                emailLink: emailLink,    // for mail address auto link
                tex: tex,
                taskList: taskList,   // Github Flavored Markdown task lists
                emoji: emoji,
                flowChart: flowChart,
                sequenceDiagram: sequenceDiagram,
                previewCodeHighlight: previewCodeHighlight
            });
        });

    }

    _init(path, cb){
        function createDom(str){
            return document.createElement(str);
        }

        var div = createDom('div');
        var id = 'EditorInitDom' + new Date().getTime();
        div.id = id;
        div.style.display = 'none';

        var textarea = createDom('textarea');
        div.appendChild(textarea);

        document.body.appendChild(div);
        // 
        // Editor.mdhack
        editormd(id, {
            path: path,
            codeFold: true,
            saveHTMLToTextarea: true,    //  HTML  Textarea
            searchReplace: true,
            htmlDecode: "style,script,iframe|on*",            //  HTML 
            emoji: true,
            taskList: true,
            tocm: true,         // Using [TOCM]
            tex: true,                   // TeX
            flowChart: true,             // 
            sequenceDiagram: true,       // /,
            imageUpload: true,
            imageFormats: ["jpg", "jpeg", "gif", "png", "bmp", "webp"]
        });

        // 
        var timer = setInterval(function(){
            try{
                if(_ && Diagram && flowchart){
                    clearInterval(timer);
                    document.body.removeChild(div);

                    cb();
                }
            }catch(e){
            }
        }, 100);
    }

    render(){
        let {config, ...others} = this.props;
        config = Object.assign({}, defaultShowConfig, config);

        let {id} = config;
        return (
            <div id={id} {...others}/>
        );
    }
}

Editor.EditorShow = EditorShow;
export default Editor;


